import { Fragment } from "react/cjs/react.production.min";
import { useStore } from "../../../store/store";
import css from "./Prices.module.css";

const Prices = () => {
	const dispatch = useStore()[1];

	const cartHandler = (event) => {
		const minus = event.target.closest("span").classList.contains("minus");
		const plus = event.target.closest("span").classList.contains("plus");
		const article = event.target.closest("article");

		console.log(article);
		// if (minus) {
		// 	dispatch('ADD', )
		// }
	};

	return (
		<Fragment>
			<div className={`${css["container__price"]} container`}>
				<h3 className="product-price fwb fs-700 text-vdblue">$125.00</h3>
				<span className="product-promo fwb text-orange">50%</span>
				<span className="product-origin-price linethrough text-blue fwb">$250.00</span>
			</div>
			<div className={`${css["container__cart"]} container`}>
				<div className="container__quantity">
					<span className="minus bg-center-cover-nr btn-minus" onClick={(e) => cartHandler(e)}>
						<img src="../../assets/images/icon-minus.svg" alt="icon minus" />
					</span>
					<span className="quantity fwb text-vblue">0</span>
					<span className="plus bg-center-cover-nr btn-plus" onClick={(e) => cartHandler(e)}>
						<img src="../../assets/images/icon-plus.svg" alt="icon plus" />
					</span>
				</div>
				<button className="btn-add">
					<img src="../../assets/images/icon-cart.svg" alt="cart icon" /> Add to cart
				</button>
			</div>
		</Fragment>
	);
};

export default Prices;
