import { initStore } from "./store";

const configureStore = () => {
	const actions = {
		ADD: (curState, item) => {
			let newItems = [];
			const itemIndex = curState.cart.items.findIndex((it) => it.id === item.id);
			if (itemIndex < 0) {
				newItems = item;
			}
			if (itemIndex >= 0) {
				newItems = [...curState.cart.items];
				newItems[itemIndex].quantity += item.quantity;
			}

			const newCart = { ...curState.cart, items: newItems };
			return { cart: newCart };
		},

		DELETE: (curState, item) => {
			const newItems = curState.cart.items.filter((it) => it.id !== item.id);
			const newCart = { ...curState.cart, items: newItems };
			return { cart: newCart };
		},
	};

	initStore(actions, {
		cart: {
			totalAmount: 0,
			// reduce todo
			items: [
				{
					id: "sneakers",
					price: 125.0,
					quantity: 3,
					quantityInStock: 20,
					image: `../../../assets/images/image-product-1-thumbnail.jpg`,
				},
				{
					id: "sners",
					price: 125.0,
					quantity: 3,
					image: `../../../assets/images/image-product-1-thumbnail.jpg`,
				},
				{
					id: "ers",
					price: 125.0,
					quantity: 3,
					image: `../../../assets/images/image-product-1-thumbnail.jpg`,
				},
			],
		},
	});
};

export default configureStore;
